body {
  font: 22px "Century Gothic", sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

p {  
  display: block;
  margin-block-start: 0.7em;
  margin-block-end: 0.7em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

.status-info {
  margin-left: 20px;
  color: rgb(208,33,41);
}

.title {
  display: flex;
  flex-direction: column;
  margin-top: auto;
  margin-left: auto;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  color: rgb(208,33,41);
}

.button-container {
  margin-left: 1em;
}

.status {
  margin-bottom: 10px;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: column;
  margin-top: 1.2em;
  margin-left: auto;
  justify-content: center;
  align-items: center;
  min-height: 15vh;
}

.scores-container {
  margin-right: 10em;
  color: rgb(208,33,41);
}

.create-button {
  margin-right: 5px;
  width: 4.5em;
  height: 2.0em;
}

.join-button {
  width: 4.5em;
  height: 2.0em;
}

.input-class{
  margin-top: -0.1em;
}

.title-class {
  font-size: 1.5rem; 
}

.popup-class{
  height: 165px; 
}

.join-button-class {
  margin: .3125em; 
  padding: .5em 1.8em; 
  margin-bottom: 1em; 
}

.button-class {
  margin: .3125em; 
  padding: .5em 1.8em; 
}

.control {
  position: absolute;
  top: 400px;
  left: 200px;
}

.pawn {
  position: absolute;
  transition: 1s ease-in-out;
  background: white;
}

#pawn1 {
  border: 2px solid red;
  left: 20px;
  top: 160px;
}

#pawn2 {
  border: 2px solid blue;
  left: 70px;
  top: 250px;
}


.square {
	background: white;
  text-align: center;
  overflow: hidden;
}

.detail {
  width: 125px;
  visibility: hidden;
  position: absolute;
  display: block;
  border: 2px solid grey;
  background: lightsteelblue;
}

.square:hover > .detail{
  visibility: visible;
  font-size: smaller;
}

.playersInfo {
  border: 2px solid black;
}

.playersInfo:hover > .detail{
  visibility: visible;
}

button {
  width: 10em;
  height: 2.0em;
  background-color: yellow;
}

button:hover {
  cursor: pointer;
  border: 2px black solid;
}

button:disabled {
  cursor: auto;
  background-color: white;
}

button:disabled:hover {
  cursor: not-allowed;
  border: none;
  background-color: white;
}
